.button {
  border: 2px solid #f8d24e;
  border-radius: 10px;
  padding: 12px 34px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.29;
  display: inline-block;
}

@media screen and (max-width: 1199px) {
