.icon-arrow {
  display: block;
}

.icon-arrow .icon-arrow1 {
  fill: currentColor;
  display: block;
}

.icon-arrow .icon-arrow2, .icon-arrow .icon-arrow3 {
  display: none;
}

.icon-arrow:hover .icon-arrow2 {
  fill: currentColor;
  display: block;
}

.icon-arrow:hover .icon-arrow1, .icon-arrow:hover .icon-arrow3 {
  display: none;
}

.icon-arrow:focus .icon-arrow3 {
  fill: currentColor;
  display: block;
}

.icon-arrow:focus .icon-arrow1, .icon-arrow:focus .icon-arrow2 {
  display: none;
}

